import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/shared/services/api.service';
import { LocalStoreService } from 'src/app/shared/services/local-store.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'npr-user-general-ledger-edit',
  templateUrl: './user-general-ledger-edit.component.html',
  styleUrls: ['./user-general-ledger-edit.component.scss']
})
export class UserGeneralLedgerEditComponent implements OnInit {
  getuserGCCSummarydata: any;
  userID: any;
  clicked = false;
  loginUser: string;
  userGLEditForm: FormGroup;
  editFlag: boolean = false;
  editAccFlag: boolean = false;
  currentUser: string;
  operation: string;
  userGLResp: any;
  formTouched: boolean=true;
  allow: string;
  disallow: string;
  hideAllow: boolean=true;
  hideDisAllow: boolean=true;
  deleteRowResp: any;
  constructor(private apiService : ApiService,private router: Router,private formBuilder: FormBuilder,private ls:LocalStoreService) { }

  ngOnInit(): void {
    this.currentUser = localStorage.getItem("userFromLogin");
    this.disallow = localStorage.getItem("DISALLOWUSER");
    this.allow = localStorage.getItem("ALLOWUSER");
    console.log("ytgjhgbj")
    if(this.disallow){
      if(JSON.parse(this.disallow)){
      console.log("Disallow");
      this.hideDisAllow=true;
      this.hideAllow=false;
      }
    }
    if(this.allow){
      if(JSON.parse(this.allow)){
        console.log("Allow");
        this.hideAllow=true;
        this.hideDisAllow=false;
      }
    }
    this.userGLEditForm = this.formBuilder.group({
      userGlCodeId: ["", ],
      type:["GL", ],
      permission: ["", [Validators.required]],
      userId: ["", Validators.required],
      medUserGlCodeDetails: this.formBuilder.array([this.createmedUserGlCodeDetailsGroup()]),
      creatorId: ["", ],
      createdTime: ["", ],
      approverId: ["", ],
      approvedTime: ["", ],
      modNo: [],
      approvedStatus: ["", ],
      approvedEver: ["", ],
      recordStatus:["", ],
    });
    this.getuserGCCdata();
  }
  private createmedUserGlCodeDetailsGroup(): FormGroup {
    return new FormGroup({
      'id': new FormControl(''),
      'glCode': new FormControl(''),
      'glDesc': new FormControl(''),
      'userGlCodeId': new FormControl('')
    })
  }
  public adduserGLEditFormGroup() {
    const medUserGlCodeDetails = this.userGLEditForm.get('medUserGlCodeDetails') as FormArray
    medUserGlCodeDetails.push(this.createmedUserGlCodeDetailsGroup())
  }
  getuserGCCdata(){
    this.apiService.getuserGCCSummary().subscribe((summaryData) => {
      this.getuserGCCSummarydata = summaryData;
      var retrievedObject = localStorage.getItem('getuserGCCSummarydata');
      this.getuserGCCSummarydata = JSON.parse(retrievedObject);
      this.userID = this.getuserGCCSummarydata.userId;
      this.auditLogforGCC();
      this.userGLEditForm.controls.userId.setValue(this.getuserGCCSummarydata.userId);
      const permission = this.getuserGCCSummarydata.permission;
      const userGlCodeId = this.getuserGCCSummarydata.userGlCodeId;
      this.userGLEditForm.controls.permission.setValue(permission);
      this.userGLEditForm.controls.userGlCodeId.setValue(userGlCodeId);
  });
  this.addGCCRowdata.removeAt(0);
  this.getuserGCCSummarydata.medUserGlCodeDetails.forEach(element => {
    const row = this.formBuilder.group({
      id : [element.id, [Validators.required]],
      glCode: [element.glCode, [Validators.required]],
      glDesc: [element.glDesc, [Validators.required]],
      userGlCodeId: [element.userGlCodeId, [Validators.required]],
    });
    this.addGCCRowdata.push(row);
  
  });
  }
  get addGCCRowdata(): FormArray {
    return this.userGLEditForm.get('medUserGlCodeDetails') as FormArray;
  }
  auditLogforGCC() {
    if (this.getuserGCCSummarydata?.approvedStatus === 'U') {
      this.getuserGCCSummarydata.approvedStatus = 'UNAUTHORIZED'
    }
    if (this.getuserGCCSummarydata?.approvedStatus === 'A') {
      this.getuserGCCSummarydata.approvedStatus = 'AUTHORIZED'
    }
    if (this.getuserGCCSummarydata?.recordStatus === 'O') {
      this.getuserGCCSummarydata.recordStatus = 'OPEN'
    }
    if (this.getuserGCCSummarydata?.recordStatus === 'C') {
      this.getuserGCCSummarydata.recordStatus = 'CLOSED'
    }
    if (this.getuserGCCSummarydata?.approvedEver === 'N') {
      this.getuserGCCSummarydata.approvedEver = 'NO'
    }
    if (this.getuserGCCSummarydata?.approvedEver === 'Y') {
      this.getuserGCCSummarydata.approvedEver = 'YES'
    }
  }
  enableEditFlag(){
    this.editFlag = true;
  }

  usertoACC(){
    console.log("inside Function!");
    
    this.apiService.getUserIdAccData(this.getuserGCCSummarydata.userId,"AC").subscribe((data) => {
     console.log(data.length)
     console.log(data);
     
     if (data.length>0){
       console.log("data is present");
       
       this.router.navigateByUrl('/system-param-maintenance/user-ac-edit');
     }else{
      this.ls.setItem('USERACCFLAG', true);
       this.router.navigateByUrl('/system-param-maintenance/user-gl-acc-maintenance-new');
     }
    })
  }
  onClickOfAuthGCC(gccFormValues) {
    if (this.getuserGCCSummarydata.creatorId === this.currentUser) {
      Swal.fire({
        title: 'Maker Cannot Authorized the Record!',
        icon: 'error'
      });
    }
    else {
      Swal.fire(
        {
          //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
          text: 'You are trying to Authorize the record. ' + ' Do you want to proceed?',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          // confirmButtonText: 'PROCEED.'
          cancelButtonText: 'NO',
          confirmButtonText: 'YES', 'icon': 'info'
        }
      ).then((result => {
        
        if (result.isConfirmed === true) {
          this.operation = 'auth';
          gccFormValues.creatorId = this.getuserGCCSummarydata.creatorId;
        this.apiService.onClckOfOperationGCCUser(this.operation,this.currentUser,gccFormValues).subscribe(authResp => {
        console.log(authResp);
        this.getuserGCCSummarydata = authResp;
        this.auditLogforGCC();
        Swal.fire({
          title: 'Record is Authorized',
          icon: 'success'
        });
      });
    }}));
    }
  }
  onClickOfCloseGCC(gccFormValues) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Close the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      
      if (result.isConfirmed === true) {
        this.operation = 'close';
        gccFormValues.creatorId = this.getuserGCCSummarydata.creatorId;
    this.apiService.onClckOfOperationGCCUser(this.operation,this.currentUser,gccFormValues).subscribe(closeResp => {
      console.log(closeResp);
      this.getuserGCCSummarydata = closeResp;
      this.auditLogforGCC();
      if (this.getuserGCCSummarydata) {
        Swal.fire({
          title: 'Record is Closed',
          icon: 'success'
        });
      }
      else {
        Swal.fire({
          title: 'Failed to Close the Record!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}))
  
  }
  onClickOfReopenGCC(gccFormValues) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Reopen the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        this.operation = 'open';
        gccFormValues.creatorId = this.getuserGCCSummarydata.creatorId;
    this.apiService.onClckOfOperationGCCUser(this.operation,this.currentUser,gccFormValues).subscribe(reopenResp => {
      console.log(reopenResp);
      this.getuserGCCSummarydata = reopenResp;
      this.auditLogforGCC();
      if (this.getuserGCCSummarydata) {
        Swal.fire({
          title: 'Record is Reopened',
          icon: 'success'
        });
      }
      else {
        Swal.fire({
          title: 'Failed to Reopen the Record!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}))
  
  }
  OndeleteofUserGCC(UserId) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Delete the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      
      if (result.isConfirmed === true) {
       const userGlCodeId= this.getuserGCCSummarydata.userGlCodeId;
    this.apiService.deleteUserGCC(userGlCodeId,UserId).subscribe(resp => {
      if (resp == true) {
        Swal.fire({
          title: 'Record Is Deleted',
          icon: 'success'
        });
        this.router.navigateByUrl('/system-param-maintenance/user-gl-acc-maintenance-summary');
      }
      else {
        Swal.fire({
          title: 'Failed to Delete User Data!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}));
  }
  deleteGLRowData(data,i) {
    const medUserGlCodeDetails = this.userGLEditForm.get('medUserGlCodeDetails') as FormArray
    // 'id': new FormControl(''),
    // 'userGlCodeId': new FormControl('')
    if(medUserGlCodeDetails.at(i).get('glCode').value && medUserGlCodeDetails.at(i).get('glDesc').value){
      // transactionData.removeAt(i);
      console.log("this.medUserGlCodeDetails.formValues",data, data.id);
      this.apiService.deleteuserGLRow(data.id).subscribe(resp => {
        this.deleteRowResp = resp;
        if (this.deleteRowResp == true) {
          {
            Swal.fire({
              title: 'Deleted Row Data!',
              icon: 'success'
            });
          }
          medUserGlCodeDetails.removeAt(i);
          // this.submitFlag = true;
        }else {
          Swal.fire({
            title: 'Failed to Delete Row Data!',
            icon: 'error'
          });
        }
      },(error:any) => {
          if (HttpErrorResponse) {
            Swal.fire({
              text: 'Server Error!',
              icon: 'error'
            });
          }
        });
    }
    else{
      // transactionData.removeAt(i);
    }
  
}
  onSubmitGlForm(userGLFormValue) {
    userGLFormValue.creatorId = this.currentUser;
    const userID = userGLFormValue.userId;
    const permission = userGLFormValue.permission;
    this.operation = 'update';
    this.apiService.updateUserGlCodeData(userGLFormValue,this.operation,this.currentUser).subscribe(savetrnres => {
      this.getuserGCCSummarydata = savetrnres;
      if (this.getuserGCCSummarydata) {
        // this.submitBtn = true;
        // this.submit1 = false;
        // this.toastService.successMessage('Data Saved Successfully!.', '');
        Swal.fire({
          title: 'Record is Updated',
          icon: 'success'
        });
        this.auditLogforGCC();
        this.formTouched=!this.userGLEditForm.touched;
        // this.userGLForm.reset();
        this.editFlag = false;
      }
      else {
        // this.toastService.errorMessage('Failed to Save Data!', '');
        Swal.fire({
          title: 'Failed to Update Data.',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }

}
