  auditlog() {
    if (this.getuserACCSummarydata.recordStatus == 'O') {
      this.getuserACCSummarydata.recordStatus = 'OPEN';
    } else if (this.getuserACCSummarydata.recordStatus == 'C') {
      this.getuserACCSummarydata.recordStatus = 'CLOSE';
    }
    if (
      this.getuserACCSummarydata.approvedStatus == 'A' ||
      this.getuserACCSummarydata.approvedStatus == 'Authorized'
    ) {
      this.getuserACCSummarydata.approvedStatus = 'AUTHORIZED';
    } else if (
      this.getuserACCSummarydata.approvedStatus == 'U' ||
      this.getuserACCSummarydata.approvedStatus == 'Unauthorized'
    ) {
      this.getuserACCSummarydata.approvedStatus = 'UNAUTHORIZED';
    }
    if (this.getuserACCSummarydata.firstTimeAuth == 'Y') {
      this.getuserACCSummarydata.firstTimeAuth = 'YES';
    } else if (this.getuserACCSummarydata.firstTimeAuth == 'N') {
      this.getuserACCSummarydata.firstTimeAuth = 'NO';
    }
  }
  auditLogforGCC() {
    if (this.getuserGCCSummarydata.approvedStatus === 'U') {
      this.getuserGCCSummarydata.approvedStatus = 'UNAUTHORIZED'
    }
    if (this.getuserGCCSummarydata.approvedStatus === 'A') {
      this.getuserGCCSummarydata.approvedStatus = 'AUTHORIZED'
    }
    if (this.getuserGCCSummarydata.recordStatus === 'O') {
      this.getuserGCCSummarydata.recordStatus = 'OPEN'
    }
    if (this.getuserGCCSummarydata.recordStatus === 'C') {
      this.getuserGCCSummarydata.recordStatus = 'CLOSED'
    }
    if (this.getuserGCCSummarydata.approvedEver === 'N') {
      this.getuserGCCSummarydata.approvedEver = 'NO'
    }
    if (this.getuserGCCSummarydata.approvedEver === 'Y') {
      this.getuserGCCSummarydata.approvedEver = 'YES'
    }
  }

