import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/shared/services/api.service';
import { LocalStoreService } from 'src/app/shared/services/local-store.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'npr-user-account-class-edit',
  templateUrl: './user-account-class-edit.component.html',
  styleUrls: ['./user-account-class-edit.component.scss']
})
export class UserAccountClassEditComponent implements OnInit {
  getuserGCCSummarydata: any;
  userID: any;
  clicked = false;
  loginUser: string;
  accountClassEditForm: FormGroup;
  getuserACCSummarydata: any;
  editFlag: boolean = false;
  editAccFlag: boolean = false;
  currentUser: string;
  operation: string;
  userGLResp: any;
  formTouched: boolean=true;
  hideAllowAcc: boolean=true;
  hideDisAllowAcc: boolean=true;
  disallowglCode: string;
  allowglCode: string;
  deleteRowResp: any;
  isDisabledSave:boolean =true;
  constructor(private apiService : ApiService,private router: Router,private formBuilder: FormBuilder,private ls:LocalStoreService) { }

  ngOnInit(): void {
    this.currentUser = localStorage.getItem("userFromLogin");
    this.disallowglCode = localStorage.getItem("DISALLOWACCCLASSUSER");
    this.allowglCode = localStorage.getItem("ALLOWACCCLASSUSER");
    console.log(this.allowglCode,this.disallowglCode);
    if(this.disallowglCode){
      if(JSON.parse(this.disallowglCode)){
      console.log("Disallow");
      this.hideDisAllowAcc=true;
      this.hideAllowAcc=false;
      }
    }
    if(this.allowglCode){
      if(JSON.parse(this.allowglCode)){
        console.log("Allow");
        this.hideAllowAcc=true;
        this.hideDisAllowAcc=false;
      }
    }
    this.accountClassEditForm = this.formBuilder.group({
      userGlCodeId: ["", ],
      type:["AC", ],
      permission: ["", [Validators.required]],
      userId: ["", Validators.required],
      medUserGlCodeDetails: this.formBuilder.array([this.createaccountClassDataGroup()]),
      creatorId: ["", ],
      createdTime: ["", ],
      approverId: ["", ],
      approvedTime: ["", ],
      versionNo: [],
      approvedStatus: ["", ],
      approvedEver: ["", ],
      recordStatus:["", ],
    });
    this.getuserACCdata();
  }
  private createaccountClassDataGroup(): FormGroup {
    return new FormGroup({
      'id': new FormControl(''),
      'glCode': new FormControl(''),
      'glDesc': new FormControl(''),
      'userGlCodeId': new FormControl(''),
    })
  }
  getuserACCdata(){
    this.apiService.getuserACCSummary().subscribe((summaryData) => {
      this.getuserACCSummarydata = summaryData;
      var retrievedObject = localStorage.getItem('getuserACCSummarydata');
      this.getuserACCSummarydata = JSON.parse(retrievedObject);
      this.userID = this.getuserACCSummarydata.userId;
      this.auditLogforACC();
      const permission = this.getuserACCSummarydata.permission;
      this.accountClassEditForm.controls.userId.setValue(this.getuserACCSummarydata.userId);
      const userGlCodeId = this.getuserACCSummarydata.userGlCodeId;
      this.accountClassEditForm.controls.permission.setValue(permission);
      this.accountClassEditForm.controls.userGlCodeId.setValue(userGlCodeId);
  });
  this.addACCRowdata.removeAt(0);
  this.getuserACCSummarydata.medUserGlCodeDetails.forEach(element => {
    const row = this.formBuilder.group({
      id : [element.id, [Validators.required]],
      glCode: [element.glCode, [Validators.required]],
      glDesc: [element.glDesc, [Validators.required]],
      userGlCodeId: [element.userGlCodeId, [Validators.required]],
    });
    this.addACCRowdata.push(row);
  
  });
  }
  get addACCRowdata(): FormArray {
    return this.accountClassEditForm.get('medUserGlCodeDetails') as FormArray;
  }
  public addaccountClassEditFormGroup() {
    const medUserGlCodeDetails = this.accountClassEditForm.get('medUserGlCodeDetails') as FormArray
    medUserGlCodeDetails.push(this.createaccountClassDataGroup())
  }
  auditLogforACC() {
    if (this.getuserACCSummarydata?.approvedStatus === 'U') {
      this.getuserACCSummarydata.approvedStatus = 'UNAUTHORIZED'
    }
    if (this.getuserACCSummarydata?.approvedStatus === 'A') {
      this.getuserACCSummarydata.approvedStatus = 'AUTHORIZED'
    }
    if (this.getuserACCSummarydata?.recordStatus === 'O') {
      this.getuserACCSummarydata.recordStatus = 'OPEN'
    }
    if (this.getuserACCSummarydata?.recordStatus === 'C') {
      this.getuserACCSummarydata.recordStatus = 'CLOSED'
    }
    if (this.getuserACCSummarydata?.approvedEver === 'N') {
      this.getuserACCSummarydata.approvedEver = 'NO'
    }
    if (this.getuserACCSummarydata?.approvedEver === 'Y') {
      this.getuserACCSummarydata.approvedEver = 'YES'
    }
  }
  enableEditFlag(){
    this.editFlag = true;
  }

  
  onClickOfAuthACC(accFormValues) {
    if (this.getuserACCSummarydata.creatorId === this.currentUser) {
      Swal.fire({
        title: 'Maker Cannot Authorized the Record!',
        icon: 'error'
      });
    }
    else {
      Swal.fire(
        {
          //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
          text: 'You are trying to Authorize the record. ' + ' Do you want to proceed?',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          // confirmButtonText: 'PROCEED.'
          cancelButtonText: 'NO',
          confirmButtonText: 'YES', 'icon': 'info'
        }
      ).then((result => {
        
        if (result.isConfirmed === true) {
          this.operation = 'auth';
          accFormValues.creatorId = this.getuserACCSummarydata.creatorId;
        this.apiService.onClckOfOperationACCUser(this.operation,this.currentUser,accFormValues).subscribe(authResp => {
        console.log(authResp);
        this.getuserACCSummarydata = authResp;
        this.auditLogforACC();
        Swal.fire({
          title: 'Record is Authorized',
          icon: 'success'
        });
      });
    }}));
    }
  }
  onInputChange(){
    this.isDisabledSave= false;
  }
  userGeneral(){
    console.log("inside Function!");
    
     this.apiService.getUserIdGlData(this.userID,"GL").subscribe((data) => {
      console.log(data.length)
      if (data.length>0){
        console.log("data is present");
        
        this.router.navigateByUrl('/system-param-maintenance/user-gl-edit');
      }else{
        this.ls.setItem('USERGLFLAG', true);
        this.router.navigateByUrl('/system-param-maintenance/user-gl-acc-maintenance-new');
      }
     })
 
   }
  onClickOfCloseACC(accFormValues) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Close the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        this.operation = 'close';
        accFormValues.creatorId = this.getuserACCSummarydata.creatorId;
    this.apiService.onClckOfOperationACCUser(this.operation,this.currentUser,accFormValues).subscribe(closeResp => {
      console.log(closeResp);
      this.getuserACCSummarydata = closeResp;
      this.auditLogforACC();
      if (this.getuserACCSummarydata) {
        Swal.fire({
          title: 'Record is Closed',
          icon: 'success'
        });
      }
      else {
        Swal.fire({
          title: 'Failed to Close the Record!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}))
  
  }
  onClickOfReopenACC(accFormValues) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Reopen the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        this.operation = 'open';
        accFormValues.creatorId = this.getuserACCSummarydata.creatorId;
    this.apiService.onClckOfOperationACCUser(this.operation,this.currentUser,accFormValues).subscribe(closeResp => {
      console.log(closeResp);
      this.getuserACCSummarydata = closeResp;
      this.auditLogforACC();
      if (this.getuserACCSummarydata) {
        Swal.fire({
          title: 'Record is Reopened',
          icon: 'success'
        });
      }
      else {
        Swal.fire({
          title: 'Failed to Reopen the Record!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}))
  
  }
  OndeleteofUserACC(UserId) {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Delete the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      
      if (result.isConfirmed === true) {
       const userGlCodeId= this.getuserACCSummarydata.userGlCodeId;
    this.apiService.deleteUserACC(userGlCodeId,UserId).subscribe(resp => {
      if (resp == true) {
        Swal.fire({
          title: 'Record Is Deleted',
          icon: 'success'
        });
        this.router.navigateByUrl('/system-param-maintenance/user-gl-acc-maintenance-summary');
      }
      else {
        Swal.fire({
          title: 'Failed to Delete User Data!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}));
  }
  deleteACRowData(data,i) {
    const medUserGlCodeDetails = this.accountClassEditForm.get('medUserGlCodeDetails') as FormArray
    if(medUserGlCodeDetails.at(i).get('glCode').value && medUserGlCodeDetails.at(i).get('glDesc').value){
      // transactionData.removeAt(i);
      console.log("this.medUserGlCodeDetails.formValues",data, data.id);
      this.apiService.deleteuserAccountClassRow(data.id).subscribe(resp => {
        this.deleteRowResp = resp;
        if (this.deleteRowResp == true) {
          {
            Swal.fire({
              title: 'Deleted Row Data!',
              icon: 'success'
            });
          }
          medUserGlCodeDetails.removeAt(i);
          // this.submitFlag = true;
        }else {
          Swal.fire({
            title: 'Failed to Delete Row Data!',
            icon: 'error'
          });
        }
      },(error:any) => {
          if (HttpErrorResponse) {
            Swal.fire({
              text: 'Server Error!',
              icon: 'error'
            });
          }
        });
    }
    else{
      // transactionData.removeAt(i);
    }
  
}
  onSubmitaccountClassEditForm(accountClassForm) {
    accountClassForm.creatorId = this.currentUser;
    let accountData = accountClassForm.medUserGlCodeDetails;
    this.operation = 'update';
    this.apiService.updateUserACCCodeData(accountClassForm,this.operation,this.currentUser).subscribe(saveaccresp => {
      this.getuserACCSummarydata = saveaccresp;
      if (this.getuserACCSummarydata) {
        localStorage.setItem("ALLOWACCCLASSUSER", this.getuserACCSummarydata);
        // this.submitAccBtn = true;
        this.editFlag = false;
        // this.submit2 = false;
        Swal.fire({
          title: 'Record is Updated',
          icon: 'success'
        });
        // this.accountClassForm.reset();
      }
      else {
        Swal.fire({
          title: 'Failed to Update Data.',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }


}
