import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/shared/services/api.service';
import { LocalStoreService } from 'src/app/shared/services/local-store.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'npr-account-class-edit',
  templateUrl: './account-class-edit.component.html',
  styleUrls: ['./account-class-edit.component.scss']
})
export class AccountClassEditComponent implements OnInit {
  accountMaintenanceEditForm: FormGroup;
  permissionvalue: Array<any> = ['Allow', 'Disallow'];
  permissionvalue1: Array<any> = ['Allow', 'Disallow'];
  currentUser: string;
  globalaccacsummryData: any;
  allow: string;
  disallow: string;
  hideAllow: boolean=true;
  hideDisAllow: boolean=true;
  hideAllowAcc: boolean=true;
  hideDisAllowAcc: boolean=true;
  disallowAccClass: string;
  allowAccClass: string;
  clicked = false;
  editFlag: boolean = false;
  operation: string;
  userID: any;
  trnCod: any;
  trnDescp: any;
  formTouched: boolean=true;
  accformValues: any;
  saveaccResp: any;
  accCod: any;
  accClassDes: any;
  deleteRowResp: boolean;
  globalglcacsummryData: any;

  constructor(private apiService : ApiService,private router: Router,private formBuilder: FormBuilder,private ls:LocalStoreService) { }

  ngOnInit(): void {
    this.currentUser = localStorage.getItem("userFromLogin");
    this.disallowAccClass = localStorage.getItem("DISALLOWACCCLASS");
    this.allowAccClass = localStorage.getItem("ALLOWACCCLASS");
    console.log(this.disallowAccClass,this.allowAccClass);
    if(this.disallowAccClass){
      if(JSON.parse(this.disallowAccClass)){
      console.log("Disallow");
      this.hideDisAllowAcc=true;
      this.hideAllowAcc=false;
      }
    }
    else{
      this.hideDisAllowAcc=false;
      this.hideAllowAcc=true;
    }
    if(this.allowAccClass){
      if(JSON.parse(this.allowAccClass)){
        console.log("Allow");
        this.hideAllowAcc=true;
        this.hideDisAllowAcc=false;
      }
    }
    else{
      this.hideAllowAcc=false;
        this.hideDisAllowAcc=true;
    }
    this.accountMaintenanceEditForm = this.formBuilder.group({
      globalGlCodeId: ["", ],
      type:["AC", ],
      permission: ["", [Validators.required]],
      medGlobalGlCodeDetails: this.formBuilder.array([this.createglobalAccountGroup()]),
      creatorId: ["", ],
      createdTime: ["", ],
      approverId: ["", ],
      approvedTime: ["", ],
      modNo: [],
      approvedStatus: ["", ],
      approvedEver: ["", ],
      recordStatus:["", ],
      // activeTab : ["", ],
    });
    this.getGlobalAccSummary();
  }
  private createglobalAccountGroup(): FormGroup {
    return new FormGroup({
      'id': new FormControl(''),
      'glCode': new FormControl(''),
      'glDesc': new FormControl(''),
      'globalGlCodeId':new FormControl(''),
    })
  }
  public addAccountFormGroup() {
    const medGlobalGlCodeDetails = this.accountMaintenanceEditForm.get('medGlobalGlCodeDetails') as FormArray
    medGlobalGlCodeDetails.push(this.createglobalAccountGroup())
  }
  goToAcc(){

    this.apiService.getGlData("GL").subscribe(udfdata => {
      this.globalglcacsummryData = udfdata;
      console.log(this.globalglcacsummryData);
      if(this.globalglcacsummryData.length > 0){
        console.log("if");
        this.router.navigateByUrl('/system-param-maintenance/global-gl-edit');
      }
      else{
        this.ls.setItem('GLOBALGLFLAG', true);
        this.router.navigateByUrl('/system-param-maintenance/global-acc-maintenance-new');
      }
  });


  }
  getGlobalAccSummary(){
    
    this.apiService.getGlobalAccSummary().subscribe((summaryData) => {
      
      this.globalaccacsummryData = summaryData;
      var retrievedObject = localStorage.getItem('globalaccacsummryData');
      this.globalaccacsummryData = JSON.parse(retrievedObject);
      this.userID = this.globalaccacsummryData.creatorId;
      this.auditLogforACC();
      const permission = this.globalaccacsummryData.permission;
      const globalGlCodeId = this.globalaccacsummryData.globalGlCodeId;
      this.accountMaintenanceEditForm.controls.permission.setValue(permission);
      this.accountMaintenanceEditForm.controls.globalGlCodeId.setValue(globalGlCodeId);
  });
  if( this.globalaccacsummryData){
  this.addACCRowdata.removeAt(0);
  
  this.globalaccacsummryData.medGlobalGlCodeDetails.forEach(element => {
    const row = this.formBuilder.group({
      id : [element.id, [Validators.required]],
      glCode: [element.glCode, [Validators.required]],
      glDesc: [element.glDesc, [Validators.required]],
      globalGlCodeId: [element.globalGlCodeId, [Validators.required]],
    });
    this.addACCRowdata.push(row);
  
  });
  }
}
  get addACCRowdata(): FormArray {
    return this.accountMaintenanceEditForm.get('medGlobalGlCodeDetails') as FormArray;
  }
  enableEditFlag(){
    this.editFlag = true;
  }
  auditLogforACC() {
    if (this.globalaccacsummryData.approvedStatus === 'U') {
      this.globalaccacsummryData.approvedStatus = 'UNAUTHORIZED'
    }
    if (this.globalaccacsummryData.approvedStatus === 'A') {
      this.globalaccacsummryData.approvedStatus = 'AUTHORIZED'
    }
    if (this.globalaccacsummryData.recordStatus === 'O') {
      this.globalaccacsummryData.recordStatus = 'OPEN'
    }
    if (this.globalaccacsummryData.recordStatus === 'C') {
      this.globalaccacsummryData.recordStatus = 'CLOSED'
    }
    if (this.globalaccacsummryData.approvedEver === 'N') {
      this.globalaccacsummryData.approvedEver = 'NO'
    }
    if (this.globalaccacsummryData.approvedEver === 'Y') {
      this.globalaccacsummryData.approvedEver = 'YES'
    }
  }
  onClickOfAuthACC(accFormValues) {
    if (this.globalaccacsummryData.creatorId === this.currentUser) {
      Swal.fire({
        title: 'Maker Cannot Authorized the Record!',
        icon: 'error'
      });
    }
    else {
      Swal.fire(
        {
          //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
          text: 'You are trying to Authorize the record. ' + ' Do you want to proceed?',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          // confirmButtonText: 'PROCEED.'
          cancelButtonText: 'NO',
          confirmButtonText: 'YES', 'icon': 'info'
        }
      ).then((result => {
        if (result.isConfirmed === true) {
          this.operation = 'auth';
          accFormValues.creatorId = this.globalaccacsummryData.creatorId;
          accFormValues.createdTime = this.globalaccacsummryData.createdTime;
          accFormValues.approvedEver = this.globalaccacsummryData.approvedEver;
          accFormValues.modNo = this.globalaccacsummryData.modNo;
          accFormValues.approvedStatus = this.globalaccacsummryData.approvedStatus;
          accFormValues.recordStatus = this.globalaccacsummryData.recordStatus;
          accFormValues.approverId = this.currentUser;
          if (accFormValues.approvedStatus === 'UNAUTHORIZED' || accFormValues.approvedStatus === 'Unauthorized') {
            accFormValues.approvedStatus = 'U'
          }
          if (accFormValues.approvedStatus === 'AUTHORIZED'|| accFormValues.approvedStatus === 'Authorized') {
            accFormValues.approvedStatus = 'A'
          }
          if (accFormValues.recordStatus == "OPEN" || accFormValues.recordStatus === 'Open') {
            accFormValues.recordStatus = 'O'
          }
          if (accFormValues.recordStatus == "CLOSE" || accFormValues.recordStatus === 'Close') {
            accFormValues.recordStatus = 'C'
          }
          if (accFormValues.approvedEver === 'NO') {
            accFormValues.approvedEver = 'N'
          }
          if (accFormValues.approvedEver === 'YES') {
            accFormValues.approvedEver = 'Y'
          }
          console.log("accFormValues",accFormValues);
        this.apiService.onClckOfOperationACC(this.operation,this.currentUser,accFormValues).subscribe(authResp => {
  
        this.globalaccacsummryData = authResp;
        this.auditLogforACC();
        Swal.fire({
          title: 'Record is Authorized',
          icon: 'success'
        });
      });
    }}));
    }
  }
  onClickOfCloseACC(accFormValues) {
    console.log(accFormValues);
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Close the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if (result.isConfirmed === true) {
        this.operation = 'close';
        accFormValues.creatorId = this.currentUser;
        accFormValues.createdTime = this.globalaccacsummryData.createdTime;
        accFormValues.approvedEver = this.globalaccacsummryData.approvedEver;
        accFormValues.approvedId = '';
        accFormValues.modNo = this.globalaccacsummryData.modNo;
        accFormValues.approvedStatus = this.globalaccacsummryData.approvedStatus;
        accFormValues.recordStatus = this.globalaccacsummryData.recordStatus;
        if (accFormValues.approvedStatus === 'UNAUTHORIZED' || accFormValues.approvedStatus === 'Unauthorized') {
          accFormValues.approvedStatus = 'U'
        }
        if (accFormValues.approvedStatus === 'AUTHORIZED'|| accFormValues.approvedStatus === 'Authorized') {
          accFormValues.approvedStatus = 'A'
        }
        if (accFormValues.recordStatus == "OPEN" || accFormValues.approvedStatus === 'Open') {
          accFormValues.recordStatus = 'O'
        }
        if (accFormValues.recordStatus == "CLOSE" || accFormValues.approvedStatus === 'Close') {
          accFormValues.recordStatus = 'C'
        }
        if (accFormValues.approvedEver === 'NO') {
          accFormValues.approvedEver = 'N'
        }
        if (accFormValues.approvedEver === 'YES') {
          accFormValues.approvedEver = 'Y'
        }
       
    this.apiService.onClckOfOperationACC(this.operation,this.currentUser,accFormValues).subscribe(closeResp => {
      console.log(closeResp);
      this.globalaccacsummryData = closeResp;
      this.auditLogforACC();
      if (this.globalaccacsummryData) {
        Swal.fire({
          title: 'Record is Closed',
          icon: 'success'
        });
      }
      else {
        Swal.fire({
          title: 'Failed to Close the Record!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}))
  
  }
  onClickOfReopenACC(accFormValues) {
    console.log(accFormValues);
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Reopen the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
     
      if (result.isConfirmed === true) {
        this.operation = 'open';
        accFormValues.creatorId = this.currentUser;
        accFormValues.createdTime = this.globalaccacsummryData.createdTime;
        accFormValues.approvedEver = this.globalaccacsummryData.approvedEver;
        accFormValues.approvedId = '';
        accFormValues.modNo = this.globalaccacsummryData.modNo;
        accFormValues.approvedStatus = this.globalaccacsummryData.approvedStatus;
        accFormValues.recordStatus = this.globalaccacsummryData.recordStatus;
        if (accFormValues.approvedStatus === 'UNAUTHORIZED' || accFormValues.approvedStatus === 'Unauthorized' ) {
          accFormValues.approvedStatus = 'U'
        }
        if (accFormValues.approvedStatus === 'AUTHORIZED' || accFormValues.approvedStatus === 'Authorized') {
          accFormValues.approvedStatus = 'A'
        }
        if (accFormValues.recordStatus == "OPEN" || accFormValues.recordStatus == "Open") {
          accFormValues.recordStatus = 'O'
        }
        if (accFormValues.recordStatus == "CLOSE" || accFormValues.recordStatus == "Close") {
          accFormValues.recordStatus = 'C'
        }
        if (accFormValues.approvedEver === 'NO') {
          accFormValues.approvedEver = 'N'
        }
        if (accFormValues.approvedEver === 'YES') {
          accFormValues.approvedEver = 'Y'
        }
       console.log(accFormValues)
    this.apiService.onClckOfOperationACC(this.operation,this.currentUser,accFormValues).subscribe(closeResp => {
      console.log(closeResp);
      this.globalaccacsummryData = closeResp;
      this.auditLogforACC();
      if (this.globalaccacsummryData) {
        Swal.fire({
          title: 'Record is Reopened',
          icon: 'success'
        });
      }
      else {
        Swal.fire({
          title: 'Failed to Reopen the Record!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}))
  
  }
  
OndeleteofUserACC(UserId) {
  Swal.fire(
    {
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: 'You are trying to Delete the record. ' + ' Do you want to proceed?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: 'NO',
      confirmButtonText: 'YES', 'icon': 'info'
    }
  ).then((result => {
    
    if (result.isConfirmed === true) {
     const globalGlCodeId= this.globalaccacsummryData.globalGlCodeId;
  this.apiService.deleteGCCuserdata(globalGlCodeId).subscribe(resp => {
    if (resp == true) {
      Swal.fire({
        title: 'Record Is Deleted',
        icon: 'success'
      });
      this.router.navigateByUrl('/system-param-maintenance/global-acc-maintenance-summary');
    }
    else {
      Swal.fire({
        title: 'Failed to Delete User Data!',
        icon: 'error'
      });
    }
  },
    error => {
      if (HttpErrorResponse) {
        Swal.fire({
          text: 'Server Error!',
          icon: 'error'
        });
      }
    });
  }}));
}
deleteglobalACRowData(data,i) {
  
  
  
  const medGlobalGlCodeDetails = this.accountMaintenanceEditForm.get('medGlobalGlCodeDetails') as FormArray
  if(medGlobalGlCodeDetails.at(i).get('glCode').value && medGlobalGlCodeDetails.at(i).get('glDesc').value){
    // transactionData.removeAt(i);
    console.log("this.medGlobalGlCodeDetails.formValues",data, data.id);
    this.apiService.deleteglobalGCRow(data.id).subscribe(resp => {
      this.deleteRowResp = resp;
      if (this.deleteRowResp == true) {
        {
          Swal.fire({
            title: 'Deleted Row Data!',
            icon: 'success'
          });
        }
        medGlobalGlCodeDetails.removeAt(i);
        this.router.navigate(['/system-param-maintenance/global-acc-maintenance-summary']);
      }else {
        Swal.fire({
          title: 'Failed to Delete Row Data!',
          icon: 'error'
        });
      }
    },(error:any) => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
  }
  else{
    // transactionData.removeAt(i);
  }

}
onSubmitAccountForm(accountMaintenanceEditForm) {
  
  accountMaintenanceEditForm.creatorId = this.currentUser;
  accountMaintenanceEditForm.approvedStatus = this.globalaccacsummryData.approvedStatus;
  accountMaintenanceEditForm.approverId = this.globalaccacsummryData.approverId;
  accountMaintenanceEditForm.approvedTime = this.globalaccacsummryData.approvedTime;
  accountMaintenanceEditForm.createdTime = this.globalaccacsummryData.createdTime;
  accountMaintenanceEditForm.recordStatus = this.globalaccacsummryData.recordStatus;
  accountMaintenanceEditForm.approvedEver = this.globalaccacsummryData.approvedEver;
  accountMaintenanceEditForm.modNo = this.globalaccacsummryData.modNo;
  if (accountMaintenanceEditForm.approvedStatus === 'UNAUTHORIZED' || accountMaintenanceEditForm.approvedStatus === 'Unauthorized') {
    accountMaintenanceEditForm.approvedStatus = 'U'
  }
  if (accountMaintenanceEditForm.approvedStatus === 'AUTHORIZED' || accountMaintenanceEditForm.approvedStatus === 'Authorized') {
    accountMaintenanceEditForm.approvedStatus = 'A'
  }
  if (accountMaintenanceEditForm.recordStatus == "OPEN" || accountMaintenanceEditForm.recordStatus == "Open") {
    accountMaintenanceEditForm.recordStatus = 'O'
  }
  if (accountMaintenanceEditForm.recordStatus == "CLOSE" || accountMaintenanceEditForm.recordStatus == "Close") {
    accountMaintenanceEditForm.recordStatus = 'C'
  }
  if (accountMaintenanceEditForm.approvedEver === 'NO') {
    accountMaintenanceEditForm.approvedEver = 'N'
  }
  if (accountMaintenanceEditForm.approvedEver === 'YES') {
    accountMaintenanceEditForm.approvedEver = 'Y'
  }

  this.operation = 'update';
    this.apiService.updateACCCodeData(accountMaintenanceEditForm,this.operation,this.currentUser).subscribe(response => {
    this.saveaccResp = response;
    this.globalaccacsummryData =response
    console.log("this.saveaccResp",this.saveaccResp);
    this.auditLogforACC();
  if (this.globalaccacsummryData) {
    this.editFlag=false;
    this.clicked=true;
    Swal.fire({
      title: 'Record is Updated',
      icon: 'success'
    });
  }
  else {
    Swal.fire({
      title: 'Failed to Update Data.',
      icon: 'error'
    });
  }
},
  error => {
    if (HttpErrorResponse) {
      Swal.fire({
        text: 'Server Error!',
        icon: 'error'
      });
    }
  });

}

}
