import { HttpErrorResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Observable, of, OperatorFunction, Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, map } from 'rxjs/operators';
import { ApiService } from 'src/app/shared/services/api.service';
import { ToastService } from 'src/app/shared/services/toast.service';
import Swal from 'sweetalert2';
import { CanDeactivateGuard, IDeactivateComponent } from 'src/app/shared/guards/can-deactivate.guard';
@Component({
  selector: 'npr-transaction-code-new',
  templateUrl: './transaction-code-new.component.html',
  styleUrls: ['./transaction-code-new.component.scss']
})
export class TransactionCodeNewComponent implements OnInit,IDeactivateComponent {
  transactionCodeForm: FormGroup;
  showList: boolean = false;
  showSubmit: boolean = false;
  UserId: any;
  userName: any;
  usersList: any;
  currentUser: string;
  editFlag: boolean = false;
  showDelete: boolean = false;
  submitAccBtn: boolean = false;
  formValues: any;
  createrId: any;
  allUserID: any;
  submit: boolean;
  formTouched: boolean=true;
  auditlogData: any;
  extName: any;
  showDialog = false;
  subject = new Subject<boolean>();
  isDisabled: boolean = true;

  constructor(private formBuilder: FormBuilder, private apiService: ApiService,private canDeactivateGuard: CanDeactivateGuard,
     private toastService: ToastService, private router : Router,private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    this.currentUser = localStorage.getItem("userFromLogin");
    this.transactionCodeForm = this.formBuilder.group({
      userId: [""],
      creatorId: [""],
      transactionData: this.formBuilder.array([this.createtransactionCodeGroup()])
      // verifiedBy: [""],
      // verifiedTime: [""],
      // recordStatus: [""],
      // verifiedOnce: [""],
      // modNo: [""],
      // verifiedStatus: [""],
      // creatorDtStamp: [""],
    });
    this.getUserName();

  }
  auditLog() {
    if (this.auditlogData.verifiedStatus =='U') {
      this.auditlogData.verifiedStatus = 'UNAUTHORIZED'
    }
    if (this.auditlogData.authStatus == 'A') {
      this.auditlogData.authStatus = 'AUTHORIZED'
    }
    if (this.auditlogData.recordStatus == 'O') {
      this.auditlogData.recordStatus = 'OPEN'
    }
    if (this.auditlogData.recordStatus == 'C') {
      this.auditlogData.recordStatus = 'CLOSED'
    }
    if (this.auditlogData.verifiedOnce == 'N') {
      this.auditlogData.verifiedOnce = 'NO'
    }
    if (this.auditlogData.verifiedOnce == 'Y') {
      this.auditlogData.verifiedOnce = 'YES'
    }
  }

  private createtransactionCodeGroup(): FormGroup {
    return new FormGroup({
      'trnDesc': new FormControl(''),
      'trnCode': new FormControl('')
    })
  }
  
  public removeData(i: number) {
    const transactionData = this.transactionCodeForm.get('transactionData') as FormArray
    transactionData.removeAt(i)
  }

  public addFormGroup() {
    const transactionData = this.transactionCodeForm.get('transactionData') as FormArray
    transactionData.push(this.createtransactionCodeGroup())
  }

  getUserName() {
    this.apiService.getUserName().subscribe(userName => {
      this.userName = userName.sort();
      this.usersList = [];
      this.allUserID = this.userName;

      var list = document.getElementById('allusers');
      this.allUserID.forEach(function (item) {
        var option = document.createElement('option');
        option.value = item;
        list.appendChild(option);
      });
      console.log(list);
      if (this.userName) {
        console.log(this.userName);
      } else {
        console.log("No name");
      }
    });
  }

  canExit() : Promise<boolean> {
    let isExit :boolean = false;
    if (this.transactionCodeForm.touched && this.formTouched==true) {
    return Swal.fire({ 
      text: 'There are unsaved changes in the screen. Would you like to navigate to other screen?',
      showCancelButton: true,
      confirmButtonColor: '#E6224A',
      cancelButtonColor: '#011945',
      cancelButtonText: 'NO',
      confirmButtonText: 'YES',
      icon: 'info',
    }).then((result) => {
      if (result.isConfirmed === true) {
        isExit=true;
        this.cdr.markForCheck();
           return isExit;
      }
      else{
        isExit=false;
        this.cdr.markForCheck();
        return isExit;
      }
    
    });
  }
 else{
  isExit=true;
  return new Promise((resolve, reject) => {
    if (isExit === true) {
      isExit=true;
      this.cdr.markForCheck();
      resolve(true);
    }
  
  })
 }
    }
  // long id;
	// String trnCode;
	// String trnDesc;
	// String userId;
  onSubmit(transactionCodeForm) {
    this.formValues = transactionCodeForm.transactionData;
    this.createrId = transactionCodeForm.userId;
    console.log("transactionCodeForm",this.formValues, this.createrId,this.currentUser );
    
        this.apiService.saveTrnCode(this.formValues,
          this.createrId,this.currentUser).subscribe(res => {
            this.auditlogData = res;
            this.auditLog();
            console.log("Audit Data",this.auditlogData, this.auditlogData.creatorId);
            if (this.auditlogData) {
              Swal.fire({
                title: 'Record is Created',
                icon: 'success'
              });
              this.submit = true;
              this.formTouched=!this.transactionCodeForm.touched;
            }
            else {
              // this.toastService.errorMessage('Failed to Save Data!', '');
              Swal.fire({
                title: 'Failed to Save Data.',
                icon: 'error'
              });
            }
          },
            error => {
              if (HttpErrorResponse) {
                Swal.fire({
                  text: 'Server Error!',
                  icon: 'error'
                });
              }
            });

  }
  enableEditFlag() {
    this.submit = false;
    this.showSubmit = true;
  }
  onInputChange(){
    console.log('input change: ', this.isDisabled);
    console.log('transactionCodeForm.controls.transactionData.invalid ', this.transactionCodeForm.controls.transactionData.invalid);
    
    if(!this.submit){ this.isDisabled = false;}
  }
  
  updateSubmitofForm(transactionCodeForm) {
    this.formValues = transactionCodeForm.transactionData;
    this.createrId = transactionCodeForm.userId;
    console.log("transactionCodeForm",this.formValues, this.createrId,this.currentUser );
    
        this.apiService.saveTrnCode(this.formValues,
          this.createrId,this.currentUser).subscribe(res => {
            this.auditlogData = res;
            if (this.auditlogData) {
              Swal.fire({
                title: 'Record is Created',
                icon: 'success'
              });
              this.showSubmit = false;
              this.formTouched=!this.transactionCodeForm.touched;
            }
            else {
              // this.toastService.errorMessage('Failed to Save Data!', '');
              Swal.fire({
                title: 'Failed to Save Data.',
                icon: 'error'
              });
            }
          },
            error => {
              if (HttpErrorResponse) {
                Swal.fire({
                  text: 'Server Error!',
                  icon: 'error'
                });
              }
            });

  }
  onClickOfAuth() {
    if (this.auditlogData.creatorId == this.currentUser) {
      Swal.fire({
        title: 'Maker Cannot Authorized the Record!',
        icon: 'error'
      });
    } else {
      Swal.fire(
        {
          //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
          text: 'You are trying to Authorize the record. ' + ' Do you want to proceed?',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          // confirmButtonText: 'PROCEED.'
          cancelButtonText: 'NO',
          confirmButtonText: 'YES', 'icon': 'info'
        }
      ).then((result => {
        console.log("this is reopen ", result);
        if (result.isConfirmed === true) {
          let payload:any={}
      
        payload.externalSystemName= this.auditlogData.extSysName;
        payload.loggedInUser=this.currentUser,
        payload.processCode=this.auditlogData.processCode;
      this.extName =  this.auditlogData.extSysName;
      this.apiService.authProcessCodeRecord(payload).subscribe(authResp => {
        this.auditlogData = authResp;
        console.log("Auth",  this.auditlogData );
        Swal.fire({
          title: ' Record is Authorized',
          icon: 'success'
        });
        this.auditLog();
      })
    }}));
    }
  }
  onClickOfClose() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Close the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {

    this.auditlogData.modifiedBy = this.currentUser;
    this.extName =  this.auditlogData.extSysName;
    let payload:any={}
      
    payload.externalSystemName= this.auditlogData.extSysName;
    payload.loggedInUser=this.currentUser,
    payload.processCode=this.auditlogData.processCode;
    this.apiService.closingProcessCodeRecord(payload).subscribe(closeResp => {
      console.log(closeResp);
      this.auditlogData = closeResp;
      if (this.auditlogData) {
        Swal.fire({
          title: 'Record is Closed',
          icon: 'success'
        });
        this.auditLog();
      }
      else {
        Swal.fire({
          title: 'Failed to Close the Recored!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}))
  }
  onClickOfReopen() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Reopen the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        let payload:any={}
      
        payload.externalSystemName= this.auditlogData.extSysName;
        payload.loggedInUser=this.currentUser,
        payload.processCode=this.auditlogData.processCode;
    this.extName =  this.auditlogData.extSysName;
    this.apiService.reopeningProcessCodeRecord(payload).subscribe(openResp => {
      console.log(openResp);
      this.auditlogData = openResp;
      if (this.auditlogData) {
        Swal.fire({
          title: 'Record is Reopened',
          icon: 'success'
        });
        this.auditLog();
      }
      else {
        Swal.fire({
          title: 'Failed to Reoped the Recored!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}));

  }
  OndeleteofUser() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Delete the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if (result.isConfirmed === true) {
    this.extName =  this.auditlogData.extSysName;
    this.apiService.deleteTransactionData(this.auditlogData.userId,this.currentUser).subscribe(resp => {
    // this.apiService.deleteProcessRecord(this.extName,this.auditlogData.processcode,this.currentUser).subscribe(resp => {
      console.log(resp);
      if (resp === true) {
      // if (resp === "extSys has been deleted!") {
        Swal.fire({
          title: 'Record is Deleted',
          icon: 'success'
        });
        this.router.navigateByUrl('/system-param-maintenance/transaction-code-summary');
        // this.router.navigateByUrl('/external-system/processCodeMapping');
      } else {
        Swal.fire({
          text: 'Failed to Delete User Data!',
          icon: 'error'
        });
      }
    },error=>{
      console.log(error.error.message)
      if (error.error.message === "extSys has been deleted!") {
        Swal.fire({
          title: 'Record is Deleted',
          icon: 'success'
        });
        this.router.navigateByUrl('/external-system/processCodeMapping');
      } else {
        Swal.fire({
          text: 'Failed to Delete User Data!',
          icon: 'error'
        });
      }
    });
  }}));
  }
}
export class TrnDataMaster {
  id;
  trnCode;
  trnDesc;
  userId;
  verifiedBy: string;
  verifiedTime: any;
  recordStatus: string;
  verifiedOnce: string;
  modNo: number;
  verifiedStatus: string;
  creatorDtStamp: any;
  creatorId: string;
}

