import { HttpErrorResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Observable, of, Subject } from 'rxjs';
import { CanDeactivateGuard, IDeactivateComponent } from 'src/app/shared/guards/can-deactivate.guard';
import { permissionsLabels } from 'src/app/shared/models/fmosNewRolePermissions';
import { ApiService } from 'src/app/shared/services/api.service';
import { RoleService } from 'src/app/shared/services/role.service';
import { ToastService } from 'src/app/shared/services/toast.service';
import Swal from 'sweetalert2';
import { TrnDataMaster } from '../transaction-code-new/transaction-code-new.component';


@Component({
  selector: 'npr-transaction-code-edit',
  templateUrl: './transaction-code-edit.component.html',
  styleUrls: ['./transaction-code-edit.component.scss']
})
export class TransactionCodeEditComponent implements OnInit ,IDeactivateComponent {
  roleCodes = new permissionsLabels();
  transactionCodeEditForm: FormGroup;
  showList: boolean = false;
  editFlag: boolean = false;
  disablesubmit: boolean;
  UserId: any;
  userName: any;
  formTouched: boolean=true;
  usersList: any;
  masterClass = new TrnDataMaster;
  formValues = {
    trnDesc: '',
    trnCode: ''
  }
  MedTransOperationDto = {
    userId : '',
    userIdLoggedIn : ''
  }
  transactionData: Array<TrnData>;
  transactionCodeSummryData: any;
  deleteRowResp: any;
  authResponse: any;
  savetrnResponse: any;
  trnDescp: any;
  trnCod: any;
  clicked = false;
  currentUser: string;
  submitFlag: boolean = false;
  id: any;
  operation: any;
  userID: any;
  trnsid: any;
  idValue: string;
  showDialog = false;
  subject = new Subject<boolean>();
  constructor(private formBuilder: FormBuilder, private apiService: ApiService,private cdr: ChangeDetectorRef,
    private toastService: ToastService, private router: Router, private ref: ChangeDetectorRef,  private roleService: RoleService,) { }

  ngOnInit(): void {
    console.log("idValue ",localStorage.getItem('idValue'))
    this.idValue=localStorage.getItem('idValue')
    setTimeout(() => {
      this.newRolePermissions();
    }, 2000);
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);
    setTimeout(() => {
      console.log(this.roleCodes);
    }, 3000);
    this.currentUser = localStorage.getItem("userFromLogin");
    this.transactionCodeEditForm = this.formBuilder.group({
      userId: [""],
      creatorId: [""],
      transactionData: this.formBuilder.array([this.createtransactionCodeGroup()]),
      verifiedBy: [""],
      verifiedTime: [""],
      recordStatus: [""],
      verifiedOnce: [""],
      modNo: [""],
      verifiedStatus: [""],
      creatorDtStamp: [""],
    });
    this.getUserName();
    this.gettingSummaryData();
    this.auditLog();
    // var retrievedObject = localStorage.getItem('transactionCodeData');
    // console.log('retrievedObject: ', JSON.parse(retrievedObject));
    // this.transactionCodeSummryData = JSON.parse(retrievedObject);
    // console.log('transactionCodeSummryData: ', JSON.parse(retrievedObject));
  }
  newRolePermissions()
  {

    this.roleService.fetchScreenPermissions('Transaction Code Maintenance');
  }
  private createtransactionCodeGroup(): FormGroup {
    return new FormGroup({
      'id': new FormControl(''),
      'trnDesc': new FormControl(''),
      'trnCode': new FormControl('')
    })
  }

  invalidLogin(event: Event) {
    this.submitFlag = true;
  }
  onOptionsSelected(event) {
    const value = event.target.value;
    if (value) {
      this.transactionCodeEditForm.controls.userId.setValue(value);
      this.showList = false;
      console.log(value);
    }
  }
  public removeData(i: number) {
    const transactionData = this.transactionCodeEditForm.get('transactionData') as FormArray;
    transactionData.removeAt(i)
    
  }
  canExit() : Promise<boolean> {
    let isExit :boolean = false;
    if (this.transactionCodeEditForm.touched && this.formTouched==true) {
    return Swal.fire({ 
      text: 'There are unsaved changes in the screen. Would you like to navigate to other screen?',
      showCancelButton: true,
      confirmButtonColor: '#E6224A',
      cancelButtonColor: '#011945',
      cancelButtonText: 'NO',
      confirmButtonText: 'YES',
      icon: 'info',
    }).then((result) => {
      if (result.isConfirmed === true) {
        isExit=true;
        this.cdr.markForCheck();
           return isExit;
      }
      else{
        isExit=false;
        this.cdr.markForCheck();
        return isExit;
      }
    
    });
  }
 else{
  isExit=true;
  return new Promise((resolve, reject) => {
    if (isExit === true) {
      isExit=true;
      this.cdr.markForCheck();
      resolve(true);
    }
  
  })
 }
    }
 
  deleteRowData(data,i) {
    const transactionData = this.transactionCodeEditForm.get('transactionData') as FormArray;
    if(transactionData.at(i).get('trnCode').value && transactionData.at(i).get('trnDesc').value){
      // transactionData.removeAt(i);
      console.log("this.transactionCodeSummryData.formValues",data.id);
      console.log("currnt user", this.currentUser)
      this.apiService.deleteRow(data.id, this.currentUser).subscribe(resp => {
        this.deleteRowResp = resp;
        if (this.deleteRowResp == true) {
          {
            Swal.fire({
              title: 'Deleted Row Data!',
              icon: 'success'
            });
          }
          transactionData.removeAt(i);
          this.submitFlag = true;
        }else {
          Swal.fire({
            title: 'Failed to Delete Row Data!',
            icon: 'error'
          });
        }
      },(error:any) => {
          if (HttpErrorResponse) {
            Swal.fire({
              text: 'Server Error!',
              icon: 'error'
            });
          }
        });
    }
    else{
      // transactionData.removeAt(i);
    }
  }

  public addFormGroup() {
    const transactionData = this.transactionCodeEditForm.get('transactionData') as FormArray
    transactionData.push(this.createtransactionCodeGroup())
  }
  getUserName() {
    this.apiService.getUserName().subscribe(userName => {
      this.userName = userName;
      this.usersList = [];
      if (this.userName) {
        console.log(this.userName);
      } else {
        console.log("No name");
      }
    });
  }

  keyPress(user: any) {
    if (user) {
      this.showList = true;
      let list = this.userName.filter(option => option.includes(user));
      this.usersList = list;
    }
    else {
      this.showList = false;
      this.usersList = [];
    }
  }
  gettingSummaryData() {
    this.apiService.getSummaryDataTransactionCode().subscribe((summaryData) => {
      console.log(summaryData);
      this.transactionCodeSummryData = summaryData;
      console.log(this.transactionCodeSummryData);
     this.auditLog();
      var retrievedObject = localStorage.getItem('transactionCodeData');
      this.transactionCodeSummryData = JSON.parse(retrievedObject);
   
      this.userID = this.transactionCodeSummryData.userId;
      this.transactionCodeEditForm.controls.userId.setValue(this.userID);
      this.addtrnDec.removeAt(0);
      // this.trnsid= this.transactionCodeSummryData.TransCode[0].id;
      // this.trnsid=this.transactionCodeSummryData.formValues[0].id;
      this.transactionCodeSummryData.medTransCode.forEach(element => {
        const row = this.formBuilder.group({
          trnDesc: [element.trnDesc, [Validators.required]],
          trnCode: [element.trnCode, [Validators.required]],
          id: [element.id, [Validators.required]],
          userId: [element.userId, [Validators.required]],
          creatorDtStamp: [element.creatorDtStamp, [Validators.required]],
          creatorId: [element.creatorId, [Validators.required]],
          modNo: [element.modNo, [Validators.required]],
          recordStatus: [element.recordStatus, [Validators.required]],
          verifiedBy: [element.verifiedBy, [Validators.required]],
          verifiedOnce: [element.verifiedOnce, [Validators.required]],
          verifiedStatus: [element.verifiedStatus, [Validators.required]],
          verifiedTime: [element.verifiedTime, [Validators.required]],
        });
        this.addtrnDec.push(row);
      
      });
    });
  }
  get addtrnDec(): FormArray {
    return this.transactionCodeEditForm.get('transactionData') as FormArray;
  }
  enableEditFlag() {
    this.editFlag = true;
  }
  auditLog() {
    if (this.transactionCodeSummryData.verifiedStatus === 'U' || this.transactionCodeSummryData.verifiedStatus === 'Unauthorized') {
      this.transactionCodeSummryData.verifiedStatus = 'UNAUTHORIZED'
    }
    if (this.transactionCodeSummryData.verifiedStatus === 'A' || this.transactionCodeSummryData.verifiedStatus === 'Authorized') {
      this.transactionCodeSummryData.verifiedStatus = 'AUTHORIZED'
    }
    if (this.transactionCodeSummryData.recordStatus === 'O' || this.transactionCodeSummryData.recordStatus === 'Open' ) {
      this.transactionCodeSummryData.recordStatus = 'OPEN'
    }
    if (this.transactionCodeSummryData.recordStatus === 'C'|| this.transactionCodeSummryData.recordStatus === 'Closed' ) {
      this.transactionCodeSummryData.recordStatus = 'CLOSED'
    }
    if (this.transactionCodeSummryData.verifiedOnce === 'N'|| this.transactionCodeSummryData.recordStatus === 'No') {
      this.transactionCodeSummryData.verifiedOnce = 'NO'
    }
    if (this.transactionCodeSummryData.verifiedOnce === 'Y'|| this.transactionCodeSummryData.recordStatus === 'Yes' ) {
      this.transactionCodeSummryData.verifiedOnce = 'YES'
    }
  }
  onClickOfAuth() {
      Swal.fire(
        {
          //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
          text: 'You are trying to Authorize the record. ' + ' Do you want to proceed?',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          // confirmButtonText: 'PROCEED.'
          cancelButtonText: 'NO',
          confirmButtonText: 'YES',
          icon: "info",
          iconColor: "#d33",
        }
      ).then((result => {
        console.log("this is reopen ", result);
        if (result.isConfirmed === true) {
          if (this.transactionCodeSummryData.creatorId === this.currentUser) {
            Swal.fire({ text: "Maker Cannot Authorize Record!", 
            icon: "error" });
          return;
        }
          this.operation = 'verify';
        this.MedTransOperationDto.userId = this.userID;
        this.MedTransOperationDto.userIdLoggedIn = this.currentUser;
        console.log("this.MedTransOperationDto",this.MedTransOperationDto);
        this.apiService.onClckOfAuthTransactionCode(this.operation, this.MedTransOperationDto).subscribe(authResp => {
        console.log(authResp);
        this.transactionCodeSummryData = authResp;
        this.auditLog();
        Swal.fire({
          title: 'Record is Authorized',
          icon: 'success'
        });
      });
    }}));
  }
  onClickOfClose() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Close the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        this.operation = 'close';
        this.MedTransOperationDto.userId = this.userID;
        this.MedTransOperationDto.userIdLoggedIn = this.currentUser;
        console.log("this.MedTransOperationDto",this.MedTransOperationDto);
    this.apiService.onclickOfCloseTransactionCode(this.operation,this.MedTransOperationDto).subscribe(closeResp => {
      console.log(closeResp);
      this.transactionCodeSummryData = closeResp;
      this.auditLog();
      this.ref.markForCheck();
      if (this.transactionCodeSummryData) {
        Swal.fire({
          title: 'Record is Closed',
          icon: 'success'
        });
      }
      else {
        Swal.fire({
          title: 'Failed to Close the Recored!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}))

  }
  onClickOfReopen() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Reopen the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        this.operation = 'reOpen';
        this.MedTransOperationDto.userId =this.userID;
        this.MedTransOperationDto.userIdLoggedIn = this.currentUser;
    this.apiService.onclickOfReopenTransactionCode(this.operation,this.MedTransOperationDto).subscribe(openResp => {
      console.log(openResp);
      this.transactionCodeSummryData = openResp;
      this.auditLog();
      this.ref.markForCheck();
      if (this.transactionCodeSummryData) {
        Swal.fire({
          title: 'Record Is Reopened',
          icon: 'success'
        });
      }
      else {
        Swal.fire({
          title: 'Failed to Reopen the Record!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}));

  }
  OndeleteofUser(UserId,currentUser) {
    console.log("USer", UserId);
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Delete the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      
      if (result.isConfirmed === true) {
    this.apiService.deleteTransactionData(this.userID,this.currentUser).subscribe(resp => {
      if (resp == true) {
        Swal.fire({
          title: 'Record Is Deleted',
          icon: 'success'
        });
        this.router.navigateByUrl('/system-param-maintenance/transaction-code-summary');
      }
      else {
        Swal.fire({
          title: 'Failed to Delete User Data!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}));
  }
  onSubmitofForm(formData, auditData) {
    console.log(auditData);
    this.disablesubmit = true;
    formData.creatorDtStamp = auditData.creatorDtStamp;
    formData.creatorId = this.currentUser;
    formData.creatorDtStamp = auditData.creatorDtStamp;
    formData.modNo = auditData.modNo;
    formData.recordStatus = auditData.recordStatus;
    formData.verifiedBy = auditData.verifiedBy;
    formData.verifiedOnce = auditData.verifiedOnce;
    formData.verifiedStatus = auditData.verifiedStatus;
    formData.verifiedTime = auditData.verifiedTime;
    let userId = auditData.userId;
    console.log("userId ",userId);
    formData.transactionData.forEach(element => {
      formData.id = element.id;
      formData.userId = element.userId;
      console.log("formData.userId ",formData.userId);
      formData.creatorId = element.creatorId;
        })
    this.transactionData = formData.transactionData;
    for(let i=0;i<this.transactionData.length;i++) {
      this.transactionData[i].creatorId = this.currentUser;
      this.transactionData[i].creatorDtStamp = formData.creatorDtStamp;
      this.transactionData[i].modNo = formData.modNo;
      this.transactionData[i].recordStatus = formData.recordStatus;
      this.transactionData[i].verifiedBy = formData.verifiedBy;
      this.transactionData[i].verifiedOnce = formData.verifiedOnce;
      this.transactionData[i].verifiedStatus = formData.verifiedStatus;
      this.transactionData[i].verifiedTime = formData.verifiedTime;
    }
    console.log("Final Data ",userId);
    this.apiService.updateTransactionCode(this.transactionData,userId, this.currentUser).subscribe(savetrnres => {
      this.transactionCodeSummryData = savetrnres;
      if (this.transactionCodeSummryData != null) {
        Swal.fire({
          title: 'Record is Updated',
          icon: 'success'
        });
        this.auditLog();
        this.editFlag=false;
        this.formTouched=!this.transactionCodeEditForm.touched;
        this.transactionCodeEditForm.markAsUntouched();
        this.transactionCodeEditForm.markAsPristine();
      } else {
        Swal.fire({
          title: 'Failed to Update the Record!',
          icon: 'error'
        });
      }
    });
  }

}
  

export class TrnData {
  id;
  trnCode;
  trnDesc;
  userId;
  verifiedBy: string;
  verifiedTime: any;
  recordStatus: string;
  verifiedOnce: string;
  modNo: number;
  verifiedStatus: string;
  creatorDtStamp: any;
  creatorId: string;
}
export class masterClass {
  id;
  trnCode;
  trnDesc;
  userId;
  verifiedBy: string;
  verifiedTime: any;
  recordStatus: string;
  verifiedOnce: string;
  modNo: number;
  verifiedStatus: string;
  creatorDtStamp: any;
  creatorId: string;
}

