import { HttpErrorResponse } from "@angular/common/http";
import { ChangeDetectorRef, Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { ReactiveFormConfig, RxwebValidators } from "@rxweb/reactive-form-validators";
import { Observable, of, Subject } from "rxjs";
import { CanDeactivateGuard, IDeactivateComponent } from "src/app/shared/guards/can-deactivate.guard";
import { permissionsLabels } from "src/app/shared/models/fmosNewRolePermissions";
import { ChargeMaintenanceService } from "src/app/shared/services/charge-maintenance-service.service";
import { MaintenanceService } from "src/app/shared/services/maintenance.service";
import { RoleService } from "src/app/shared/services/role.service";
import { ToastService } from "src/app/shared/services/toast.service";

import Swal from 'sweetalert2';

	
@Component({
  selector: "npr-departmenrt-id-new",
  templateUrl: "./departmenrt-id-new.component.html",
  styleUrls: ["./departmenrt-id-new.component.scss"],
})
export class DepartmenrtIdNewComponent implements OnInit ,IDeactivateComponent{
  departmentForm: FormGroup;
  submitDisab: boolean=false;
  respData: any;
  index: number;
  editFlag: boolean = true;
  currentUser: string;
  roleCodes = new permissionsLabels();
  formTouched: boolean=true;
  idValue: string;
  showDialog = false;
  subject = new Subject<boolean>();
  constructor(
    private toastService: ToastService,
    private formBuilder: FormBuilder,
    private departmentService: MaintenanceService,
    private router : Router,
    private roleService: RoleService,private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    console.log("idValue ",localStorage.getItem('idValue'))
    this.idValue=localStorage.getItem('idValue')
    ReactiveFormConfig.set({"validationMessage":{"greaterThan":"Batch No To should not be less than Batch No From."}});
    setTimeout(() => {
      this.newRolePermissions();
    }, 2000);
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);
    setTimeout(() => {
      console.log(this.roleCodes);
    }, 3000);
    this.currentUser = localStorage.getItem("userFromLogin");
  
    this.buildForm("");
    if(this.idValue !== null){
      if(this.idValue !== null){
        this.departmentService.setIndex({
          index: this.idValue
         });
    }
    }
    this.departmentService.getIndex().subscribe((index) => {
      console.log(index);
      if (index.index) {
        this.index = index.index;
        this.departmentService
          .gettingDepartmentDataInSummary()
          .subscribe((dataresp) => {
            console.log(dataresp);
            if (dataresp) {
              this.getRespBasedOnId(dataresp.result, index.index);
            }
          });
      }
    });
    
  }
  getRespBasedOnId(resp, arg0) {
    this.respData = resp.find((item) => item.departId == arg0);
    console.log("obj based on departId ", this.respData);
    this.editFlag = false;
    this.buildForm(this.respData);
    this.auditLog();
  }
 
  canExit() : Promise<boolean> {
    let isExit :boolean = false;
    if (this.departmentForm.touched && this.formTouched==true) {
    return Swal.fire({ 
      text: 'There are unsaved changes in the screen. Would you like to navigate to other screen?',
      showCancelButton: true,
      confirmButtonColor: '#E6224A',
      cancelButtonColor: '#011945',
      cancelButtonText: 'NO',
      confirmButtonText: 'YES',
      icon: 'info',
    }).then((result) => {
      if (result.isConfirmed === true) {
        isExit=true;
        this.cdr.markForCheck();
           return isExit;
      }
      else{
        isExit=false;
        this.cdr.markForCheck();
        return isExit;
      }
    
    });
  }
 else{
  isExit=true;
  return new Promise((resolve, reject) => {
    if (isExit === true) {
      isExit=true;
      this.cdr.markForCheck();
      resolve(true);
    }
  
  })
 }
    }
  buildForm(item) {
    this.departmentForm = this.formBuilder.group({
      id:[item ? item.id : ""],
      departId: [item ? item.departId : "", Validators.required],
      departName: [item ? item.departName : "", [Validators.required]],
      fromSeries: [item ? item.fromSeries : "", Validators.required],
      toSeries: [item ? item.toSeries : "", [Validators.required,RxwebValidators.greaterThan({fieldName:'fromSeries' })]],
      inputBy: [item ? item.inputBy : this.currentUser],
      inputTime: [item ? item.inputTime : ""],
      verifiedBy: [item ? item.verifiedBy : ""],
      verifiedTime: [item ? item.verifiedTime : ""],
      recordStatus: [item ? item.recordStatus : ""],
      verifiedOnce: [item ? item.verifiedOnce : ""],
      modNo: [item ? item.modNo : ""],
      verifiedStatus: [item ? item.verifiedStatus : ""],
      respMsg: [item ? item.respMsg : ""],
    });
  }
  onSubmittingofChargeMaintenance(value) {
  // if(this.departmentForm.value.toSeries < this.departmentForm.value.fromSeries){
  //   Swal.fire({text:'Batch No To should not be less than Batch No From',
  // 'icon':'error'});
  // return
  // }
    if (value === "submit") {
      this.departmentForm.value.inputBy=this.currentUser
      this.departmentService
        .onSubmittingDepartment(this.departmentForm.value)
        .subscribe((deptMaintenanceResp) => {
          
          console.log(deptMaintenanceResp);
          console.log("Response", deptMaintenanceResp.respMsg );

          if (
            deptMaintenanceResp.respMsg === "Department Saved Successfully "
          ) {
            // this.submitDisab = true;
            this.respData = deptMaintenanceResp;
           this.index=this.respData.departId 
            this.auditLog();
            this.editFlag=false;
            Swal.fire({
            text:  "Record is Created",
            'icon':'success'
            }
            );
            this.formTouched=!this.departmentForm.touched;
          } else {
            this.submitDisab = true;
            this.editFlag=false;
            Swal.fire({text:deptMaintenanceResp.respMsg,
              'icon':'error'});
          }
         
        },  error=>{
          
          console.log('error resp :: ',error);
          Swal.fire(
            {text: error.error.message
            ,
          icon:'error'}
        
           );
        }
        )
      
    }
    if (value === "edit") {
this.departmentForm.value.inputBy=this.currentUser
      this.departmentService.onsaveTheEditOfDepartment(this.departmentForm.value).subscribe((deptMaintenanceResp) => {         
        console.log(deptMaintenanceResp);
          if (deptMaintenanceResp) {
            if (deptMaintenanceResp.respMsg === "Modified Record Success") {
              // this.submitDisab = true;
              this.respData = deptMaintenanceResp;
              this.auditLog();
              this.editFlag=false;
              Swal.fire(
               {text: "Record is Updated",
               'icon':'success'}
           
              );
              this.formTouched=!this.departmentForm.touched;
              this.departmentForm.markAsUntouched();
              this.departmentForm.markAsPristine();
            } else {
              Swal.fire({text:deptMaintenanceResp.respMsg});
            }
          } 

          else {
            Swal.fire({text:'Department Updation failed',
            'icon':'error'});
          }
        },
        error=>{
          
          console.log('error resp :: ',error);
          Swal.fire(
            {text: error.error.message
            ,
          icon:'error'}
        
           );
        }
        );
    }
  }
  auditLog() {
    if (this.respData.verifiedStatus === 'U') {
      this.respData.verifiedStatus = 'UNAUTHORIZED'
    }
    if (this.respData.verifiedStatus === 'A') {
      this.respData.verifiedStatus = 'AUTHORIZED'
    }
    if (this.respData.recordStatus === 'O') {
      this.respData.recordStatus = 'OPEN'
    }
    if (this.respData.recordStatus === 'C') {
      this.respData.recordStatus = 'CLOSED'
    }
    if (this.respData.verifiedOnce === 'N') {
      this.respData.verifiedOnce = 'NO'
    }
    if (this.respData.verifiedOnce === 'Y') {
      this.respData.verifiedOnce = 'YES'
    }
  }
  getChargeMaintainenceSummary() {
    this.departmentService
      .gettingDepartmentDataInSummary()
      .subscribe((dataresp) => {
        console.log("summry",dataresp);
        // this.respArray = dataresp;
        // this.dtTrigger.next();
      });
  }
  onAuthorizingTheRecord() {
    Swal.fire(

      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Authorize record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3e54b8',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
           confirmButtonText: 'YES','icon':'info',iconColor: "#d33"
      }
    ).then((result => {
      console.log("this is reopen ",result);
      if(result.isConfirmed=== true){
        this.departmentForm.value.inputBy=this.currentUser
  
        console.log("this is authorzation");
        if(this.respData.inputBy === this.currentUser){
          Swal.fire({
            text:  "Maker cannot authorize the record",
           icon:'error'
            });
            return
        }
        this.departmentService
          .onAuthDepartment(this.departmentForm.value)
          .subscribe((authResp) => {
            console.log(authResp);
            this.respData = authResp;
            this.auditLog();
     
            
            if (this.respData) {
              if (this.respData.verifiedBy != null) {
                Swal.fire(
                 {text: "Record is Authorized",
                 'icon':'success'}
                 
                  // type: "success"
                );
              } else {
                Swal.fire({
                text:  "Authorization failed for the record",
               'icon':'error'
                });
              }
            }
          });
     }}))
   
  }
  onSubmitEditMaintenance() {
    this.editFlag = true;
    console.log(this.editFlag);
  }

  onClosingTheRecord() {
    Swal.fire(

      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Close record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3e54b8',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
           confirmButtonText: 'YES','icon':'info',iconColor: "#d33"
      }
    ).then((result => {
      console.log("this is reopen ",result);
      if(result.isConfirmed=== true){
        this.departmentForm.value.inputBy=this.currentUser
        console.log("this is close");
        this.departmentService
          .onCloseOfDepartment(this.departmentForm.value)
          .subscribe((closeResp) => {
            console.log(closeResp);
            this.respData = closeResp;
            
     
            if (this.respData) {
              if (this.respData.recordStatus == "C") {
                this.auditLog()
                Swal.fire(
                { text: "Record is Closed",
              'icon':'success'}
                
                );
              } else {
                Swal.fire({
                  text:"Record closing failed",
                  'icon':'error'
                }
                  // type: "success"
                );
              }
            }
          });
     }}))
   
  }

  onReopningTheRecord() {
    Swal.fire(

      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Reopen record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3e54b8',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
           confirmButtonText: 'YES','icon':'info',iconColor: "#d33"
      }
    ).then((result => {
      console.log("this is reopen ",result);
      if(result.isConfirmed=== true){
        this.departmentForm.value.inputBy=this.currentUser
        console.log("this is reopen");
        this.departmentService
          .onReopenOfDepartment(this.departmentForm.value)
          .subscribe((reopnResp) => {
            console.log(reopnResp);
            this.respData = reopnResp;
        
     
            if (this.respData) {
              if (this.respData.recordStatus == "O") {
                this.auditLog();
                Swal.fire(
                 { text:"Record is Opened",
                'icon':'success'}
              
                  // type: "success"
                );
              } else {
                Swal.fire({text:"Record cannot be opened ",
                'icon':'error'});
              }
            }
          });
     }}))

  }
  onDeletingTheRecord() {
    Swal.fire(

      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Delete record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3e54b8',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
           confirmButtonText: 'YES','icon':'info',iconColor: "#d33"
      }
    ).then((result => {
      console.log("this is reopen ",result);
      if(result.isConfirmed=== true){
        this.departmentForm.value.inputBy=this.currentUser;
        this.departmentForm.value.id = this.respData.id;
        console.log("this is delete", this.departmentForm.value);
        this.departmentService.onDeleteOfDepartment(this.departmentForm.value.id,this.departmentForm.value.userId).subscribe((deleteResp) => {
            console.log(deleteResp);
            this.respData = deleteResp;
            if (this.respData.respMsg =="Deleted Successfully") {
              Swal.fire({
                title: 'Record is Deleted',
                icon: 'success'
              });
              this.router.navigateByUrl('/system-param-maintenance/dept-id-maintenance-summary');
            }
            else {
              Swal.fire({
                title: 'Failed to Delete Record!',
                icon: 'error'
              });
            }
          },
            error => {
              if (HttpErrorResponse) {
                Swal.fire({
                  text: 'Server Error!',
                  icon: 'error'
                });
              }
            }
            );
     }}))
    
     
  }
  numericOnly(event): boolean {
    return !isNaN(Number(event.key)) && event.key !== " ";
  }
  newRolePermissions()
  {

    this.roleService.fetchScreenPermissions('Department ID Maintenance');
  }
}
