  public removemedUserGlCodeDetails(i: number) {
    if (this.editFlag) {
      const medUserGlCodeDetails = this.userGLForm.get('medUserGlCodeDetails') as FormArray
      medUserGlCodeDetails.removeAt(i)
    }
  }
  public removeaccountClassData(i: number) {
    if (this.editFlag1) {
      const medUserGlCodeDetails = this.accountClassForm.get('medUserGlCodeDetails') as FormArray
      medUserGlCodeDetails.removeAt(i)
    }
  }
 
  canExit() : Promise<boolean> {
    let isExit :boolean = false;
    if (this.userGLForm.touched && this.formTouched==true) {
    return Swal.fire({ 
      text: 'There are unsaved changes in the screen. Would you like to navigate to other screen?',
      showCancelButton: true,
      confirmButtonColor: '#E6224A',
      cancelButtonColor: '#011945',
      cancelButtonText: 'NO',
      confirmButtonText: 'YES',
      icon: 'info',
    }).then((result) => {
      if (result.isConfirmed === true) {
        isExit=true;
        this.cdr.markForCheck();
           return isExit;
      }
      else{
        isExit=false;
        this.cdr.markForCheck();
        return isExit;
      }
    
    });
  }
 else{
  isExit=true;
  return new Promise((resolve, reject) => {
    if (isExit === true) {
      isExit=true;
      this.cdr.markForCheck();
      resolve(true);
    }
  
  })
 }
    }

  getUserName() {
    this.apiService.getUserName().subscribe(userName => {
      this.userName = userName.sort();
      this.usersList = [];
      this.allUserID = this.userName;
      var list = document.getElementById('allusers');
      this.allUserID.forEach(function (item) {
        var option = document.createElement('option');
        option.value = item;
        list.appendChild(option);
      });
      if (this.userName) {
        console.log(this.userName);
      } else {
        console.log("No name");
      }
    });
  }
  keyPress(user: any) {
    if (user) {
      this.showList = true;
      let list = this.userName.filter(option => option.includes(user));
      this.usersList = list;
    }
    else {
      this.showList = false;
      this.usersList = [];
    }
  }
  onOptionsSelected(event) {
    this.userID = event.target.value;
    if (this.userID) {
      this.userGLForm.controls.userId.setValue(this.userID);
      this.showList = false;
      this.getGlTrndata();
      console.log(this.userID);
    }
  }

