    auditLogforGCC() {
      if (this.userGLResp?.approvedStatus === 'U') {
        this.userGLResp.approvedStatus = 'UNAUTHORIZED'
      }
      if (this.userGLResp?.approvedStatus === 'A') {
        this.userGLResp.approvedStatus = 'AUTHORIZED'
      }
      if (this.userGLResp?.recordStatus === 'O') {
        this.userGLResp.recordStatus = 'OPEN'
      }
      if (this.userGLResp?.recordStatus === 'C') {
        this.userGLResp.recordStatus = 'CLOSED'
      }
      if (this.userGLResp?.approvedEver === 'N') {
        this.userGLResp.approvedEver = 'NO'
      }
      if (this.userGLResp?.approvedEver === 'Y') {
        this.userGLResp.approvedEver = 'YES'
      }
    }
    auditLogforACC() {
      if (this.accountClassResp?.approvedStatus === 'U') {
        this.accountClassResp.approvedStatus = 'UNAUTHORIZED'
      }
      if (this.accountClassResp?.approvedStatus === 'A') {
        this.accountClassResp.approvedStatus = 'AUTHORIZED'
      }
      if (this.accountClassResp?.recordStatus === 'O') {
        this.accountClassResp.recordStatus = 'OPEN'
      }
      if (this.accountClassResp?.recordStatus === 'C') {
        this.accountClassResp.recordStatus = 'CLOSED'
      }
      if (this.accountClassResp?.approvedEver === 'N') {
        this.accountClassResp.approvedEver = 'NO'
      }
      if (this.accountClassResp?.approvedEver === 'Y') {
        this.accountClassResp.approvedEver = 'YES'
      }
    }
    onClickOfAuthGCC(gccFormValues) {
      if (this.userGLResp.creatorId === this.currentUser) {
        Swal.fire({
          title: 'Maker Cannot Authorized the Record!',
          icon: 'error'
        });
      }
      else {
        Swal.fire(
          {
            //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
            text: 'You are trying to Authorize the record. ' + ' Do you want to proceed?',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            // confirmButtonText: 'PROCEED.'
            cancelButtonText: 'NO',
            confirmButtonText: 'YES', 'icon': 'info'
          }
        ).then((result => {
          
          if (result.isConfirmed === true) {
            this.operation = 'auth';
            gccFormValues.creatorId = this.userGLResp.creatorId;
          this.apiService.onClckOfOperationGCCUser(this.operation,this.currentUser,gccFormValues).subscribe(authResp => {
          console.log(authResp);
          this.userGLResp = authResp;
          this.auditLogforGCC();
          Swal.fire({
            title: 'Record is Authorized',
            icon: 'success'
          });
        });
      }}));
      }
    }
