import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CanDeactivateGuard } from 'src/app/shared/guards/can-deactivate.guard';
import { DepartmenrtIdNewComponent } from './department-id-maintenance/departmenrt-id-new/departmenrt-id-new.component';

import { DepartmentIdMaintenanceComponent } from './department-id-maintenance/department-id-maintenance/department-id-maintenance.component';
import { AcGlComponent } from './global-gcc-ac-maintenance/ac-gl/ac-gl.component';
import { AccountClassEditComponent } from './global-gcc-ac-maintenance/account-class-edit/account-class-edit.component';
import { GeneralLedgerEditComponent } from './global-gcc-ac-maintenance/general-ledger-edit/general-ledger-edit.component';
import { GlAcComponent } from './global-gcc-ac-maintenance/gl-ac/gl-ac.component';
import { GlobalGccAcMaintenanceComponent } from './global-gcc-ac-maintenance/global-gcc-ac-maintenance/global-gcc-ac-maintenance.component';
import { GlobalGccAcNewComponent } from './global-gcc-ac-maintenance/global-gcc-ac-new/global-gcc-ac-new.component';
import { TransactionCodeEditComponent } from './transaction-code-maintenance/transaction-code-edit/transaction-code-edit.component';
import { TransactionCodeMaintenanceComponent } from './transaction-code-maintenance/transaction-code-maintenance/transaction-code-maintenance.component';
import { TransactionCodeNewComponent } from './transaction-code-maintenance/transaction-code-new/transaction-code-new.component';
import { UserAccountClassEditComponent } from './user-gcc-ac-maintenance/user-account-class-edit/user-account-class-edit.component';
import { UserGccAcMaintenanceComponent } from './user-gcc-ac-maintenance/user-gcc-ac-maintenance/user-gcc-ac-maintenance.component';
import { UserGccAcNewComponent } from './user-gcc-ac-maintenance/user-gcc-ac-new/user-gcc-ac-new.component';
import { UserGeneralLedgerEditComponent } from './user-gcc-ac-maintenance/user-general-ledger-edit/user-general-ledger-edit.component';


const routes: Routes = [
  {
    path: 'transaction-code-summary',
    component: TransactionCodeMaintenanceComponent
  },
  {
    path: 'transaction-code-new',
    component: TransactionCodeNewComponent,
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path: 'transaction-code-edit',
    component: TransactionCodeEditComponent,
    canDeactivate: [CanDeactivateGuard]
  },

  {
    path: 'global-acc-maintenance-summary',
    component: GlobalGccAcMaintenanceComponent
  },
  {
    path: 'global-acc-maintenance-new',
    component: GlobalGccAcNewComponent,
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path: 'global-gl-edit',
    component: GlAcComponent
  },
  {
    path: 'global-ac-edit',
    component: AcGlComponent
  },
  {
    path: 'user-gl-acc-maintenance-summary',
    component: UserGccAcMaintenanceComponent
  },
  {
    path: 'user-gl-acc-maintenance-new',
    component: UserGccAcNewComponent,
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path: 'user-gl-edit',
    component: UserGeneralLedgerEditComponent
  },
  {
    path: 'user-ac-edit',
    component: UserAccountClassEditComponent
  },

 
  {
    path: 'dept-id-maintenance-summary',
    component: DepartmentIdMaintenanceComponent
  },
  {
    path: 'dept-id-maintenance-new',
    component: DepartmenrtIdNewComponent,
    canDeactivate: [CanDeactivateGuard]
  },
 {
    path: 'transaction-code-edit',
    component: TransactionCodeEditComponent,
    canDeactivate: [CanDeactivateGuard]
  },  

];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SystemParamMaintenanceRoutingModule { }
